@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
*{
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
}

body{
  display: flex;
  justify-content: center;
  align-items: center;

}

.container{
 
   top: 50%;
   left: 50%;
   position: absolute;
   -webkit-transform: translate(-50%, -50%);
           transform: translate(-50%, -50%);                         
   margin: 0 auto;
   width: 256px;
   text-align:center;
   border:10px solid #101116;
   background-color: #101116;
   border-radius: 10px;
 
  
}

.keypad{
   display:grid;
   grid-template-columns: repeat(4,1fr);
   grid-auto-rows: minmax(60px, auto);
}

input[type="text"] {
height: 75px;
width: 250px;
background-color: #10111600;
border:0px;
color: white;
text-align: right;

}

#clear{
  grid-column: 1/3;
  grid-row: 1;
  color: #262834;

}

#result{
  grid-column: 3/5;
  grid-row: 5;
  background-color: #262834;
  color: white;

}

#backspace{
  color: #262834;
}

button {
  
  margin: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #262834;
  color: white;
  font-weight: 500;

}

input:focus,
input:active,
button:focus,
button:active{
  outline: none;
}

.highlight{
  background-color: #56cbdb;
}



